<template>
  <div class="container">
    <!-- <van-icon name="down" /> -->
    <navbar></navbar>
    <div class="top">
      <div class="top-left">
        快捷登录
        <p>未注册的账号将自动注册为新账号</p>
      </div>
      <div class="top-right">
        <img src="../../assets/login/q_message.svg" width="73" height="73" />
      </div>
    </div>
    <van-field
      v-model="tel"
      type="tel"
      placeholder="请输入手机号"
      @click="show = true"
    />
    <van-number-keyboard
      :show="show"
      @blur="show = false"
      @input="onInput"
      @delete="onDelete"
    />
    <van-checkbox v-model="checked" shape="square"
      >同意<span>《用户协议》</span>与<span>《隐私条款》</span></van-checkbox
    >
    <div class="btn" @click="login(tel)">立即登录</div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      show: false,
      tel: "",
      checked: false,
    };
  },
  methods: {
    onInput(value) {
      this.tel += value;
    },
    onDelete() {
      this.tel = this.tel.substring(0, this.tel.length - 1);
    },
    login(tel) {
      let reg = /^1[3-9]\d{9}$/;
      if (!this.tel) {
        Toast("请填写手机号");
        return;
      }
      if (this.tel && !reg.test(this.tel)) {
        Toast.fail("电话号格式不正确！");
        return;
      }
      if (!this.checked) {
        Toast("请先勾选同意《用户协议》与《隐私条款》");
        return;
      }
      Toast.success("验证码已发送！");
      this.$router.push({ path: "verificationCode", query: { tel } });
    },
  },
};
</script>

